.App {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;

    .heart {
        color: #e74c3c;
    }

    main {
        width: 900px;
        border-radius: 20px;
        padding: 24px;
        border: 1px solid #cccccc;
        box-shadow: 0px 3px 19px -5px rgba(0,0,0,0.75);


        .heading {
            display: flex;
            justify-content: space-between;

            span {
                flex-grow: 1;
                font-size: 40px;
                font-weight: 300;
                &:first-letter {
                    text-transform:capitalize;
                }
            }
        }

        .built_by {
            text-align: center;
            margin-top: 32px;
            a {
                text-decoration: underline;
                color: inherit;
            }
        }
    }
}

@media (max-width: 500px) {
    .App {
        main {

            .heading {
                flex-direction: column;
                & > * {
                    flex-grow: 1;
                }
            }
        }
    }
}

@media (max-width: 1000px) {
    .App {
        padding: 16px;
        box-sizing: border-box;
        main {
            width: 100%;
            //margin: 8px;
            box-sizing: border-box;
            padding: 8px;

        }
    }
}
